<template>
  <div class="container py-3">
    <!-- <el-divider class="my-1"></el-divider> -->
    <div class="row overflow-auto">
      <div class="col-12 col-md-8 order-2 order-md-1">
        <img v-for="(item, index) in data.img" :key="index" class="w-100" :src="item" alt="">
      </div>
      <div class="col col-md-4 position-relative order-1 order-md-2">
        <div class="d-none d-md-block position-fixed px-3" style="top: 30%; max-width: 300px;">
          <h2 class="page-title font-weight-bolder mb-4">{{data.title}}</h2>
          <h3 class="mb-4 page-title">{{data.textContentTitle}}</h3>
          <p class="fz-20">/</p>
          <p class="d-block fz-14 mb-0"
          v-for="(item, index) in data.textContent" :key="index">
            {{item}}
          </p>
        </div>
        <div class="d-block d-md-none px-3 mt-4 mb-3">
          <h2 class="page-title font-weight-bolder">{{data.title}}</h2>
          <h3 class="mb-3 page-title">{{data.textContentTitle}}</h3>
          <p class="fz-20">/</p>
          <p class="d-block fz-14 mb-0"
          v-for="(item, index) in data.textContent" :key="index">{{item}}
          </p>
        </div>
      </div>
    </div>
    <el-divider></el-divider>
    <div class="back-page-btn" @click="$router.push('/graphic_design')">
      <span><i class="fas fa-reply"></i></span>
    </div>
  </div>
</template>

<script>
import hoolanaGif from '@/assets/img/web_index/hoolana_gif.gif';
import hoolanaImg from '@/assets/img/web_index/hoolana_fullpage.jpg';
import lazyImg1 from '@/assets/img/web_index/little_lazy_day_gif.gif';
import lazyImg2 from '@/assets/img/web_index/little_lazy_day_1.jpeg';
import lazyImg3 from '@/assets/img/web_index/little_lazy_day_gif_2.gif';
import lazyImg4 from '@/assets/img/web_index/little_lazy_day_2.jpeg';
import lazyImg5 from '@/assets/img/web_index/little_lazy_day_3.jpeg';
import lazyImg6 from '@/assets/img/web_index/little_lazy_day_4.jpeg';
import summerImg from '@/assets/img/web_index/summer_fling_fullpage.jpg';
import springGif from '@/assets/img/web_index/the_scent_of_spring_gif.gif';
import springImg from '@/assets/img/web_index/the_scent_of_spring_fullpage.jpg';
import springImg2 from '@/assets/img/web_index/the_scent_of_spring_2.jpeg';
import tranquilImg from '@/assets/img/web_index/tranquil_fullpage.jpg';
import tranquilGif from '@/assets/img/web_index/tranquil.gif';

export default {
  data() {
    return {
      data: {
        title: '',
        textContentTitle: '',
        textContent: '',
        img: '',
      },
    };
  },
  mounted() {
    const theme = this.$route.params.layout;
    if (theme === 'hoolana') {
      this.data.img = [hoolanaGif, hoolanaImg];
      this.data.title = 'Hoolana';
      this.data.textContentTitle = 'Hope can be a powerful force. When you know what you hope for most and hold it like a light within you, you can make things happen.';
      this.data.textContent = ['', '', ''];
    } else if (theme === 'lazy_day') {
      this.data.img = [lazyImg1, lazyImg2, lazyImg3, lazyImg4, lazyImg5, lazyImg6];
      this.data.title = 'A Little Lazy Day';
      this.data.textContentTitle = 'You dont always need a plan, its ok to be lazy.';
      this.data.textContent = ['', '', '', ''];
    } else if (theme === 'summer_filing') {
      this.data.img = [summerImg];
      this.data.title = 'Summer Fling';
      this.data.textContentTitle = 'Smell the sea and feel the sky. Let your soul and spirit fly.';
      this.data.textContent = [''];
    } else if (theme === 'scent_of_spring') {
      this.data.img = [springGif, springImg, springImg2];
      this.data.title = 'The Scent Of Spring';
      this.data.textContentTitle = 'Its spring again. I can hear the birds sing again. See the flowers start to bud. See young people fall in love.';
      this.data.textContent = ['', '', ''];
    } else if (theme === 'tranquil') {
      this.data.img = [tranquilGif, tranquilImg];
      this.data.title = 'TRANQUIL';
      this.data.textContentTitle = 'Life with a sense of belonging is to give yourself more space.';
      this.data.textContent = ['', '', '', ''];
    }
  },
  beforeDestroy() {
  },
  methods: {
  },
};
</script>

